















































  import poster from '../../components/poster.vue'
  
	export default {
    components: {
      poster
    },
		data() {
			return {
				inviteInfo: null,
        popShareType: false,
        posterData: {},
        shorturl: '',
        clickShareLinking: false
			};
		},
    computed: {
      userInfo() {
        return this.$store.state.userInfo
      }
    },
		onLoad() {
      if (this.userInfo.role !== 0) {
        uni.setNavigationBarTitle({
        	title: 'VIP邀请'
        })
      } else {
        uni.setNavigationBarTitle({
        	title: '邀请好友'
        })
      }
      this.$http({
        url: this.$apis.articles,
        method: 'GET',
        data: {
          cat_id: this.userInfo.role !== 0 ? 4 : 3
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.inviteInfo = data.data
            this.inviteInfo.forEach(item => {
              item.content = item.content.replace(/\<img/gi, '<img style="display:block;max-width:100%;height:auto" ')
            })
          } else {
            uni.showToast({
            	title: '获取信息失败',
              icon: 'none'
            })
          }
        }
      })
		},
    methods: {
      showTips() {
        uni.showModal({
        	title: '提示',
          content: '您是临时用户，无权使用此功能，请先免费升级为普通会员继续使用。',
          cancelText: '知道了',
          confirmText: '去升级',
          success: res => {
          	if (res.confirm) {
              this.$navigateTo({
              	url: '/pages/mine/agent'
              })
            }
          }
        })
      },
      showSharePop() {
        if (this.userInfo.role === 0) {
          this.showTips()
          return
        }
        this.popShareType = true
      },
      hideSharePop() {
        this.popShareType = false
      },
      clickShareLink() {
        if (this.clickShareLinking) {
          uni.showToast({
          	title: '分享中，请勿频繁操作',
            icon: 'none'
          })
          return
        }
        this.clickShareLinking = true
        let str = this.$constant.shareUrl + '?pid=' + this.userInfo.id
        uni.getProvider({
        	service: 'share',
          success: res => {
            if (~res.provider.indexOf('weixin')) {
              uni.share({
              	provider: 'weixin',
              	type: 0,
                title: this.$store.state.config.system_nick_name,
                scene: 'WXSceneSession',
                summary: this.$store.state.config.platform_info,
                href: str,
                imageUrl: this.$store.state.config.logo,
              	success: res => {
                  console.log(res)
                },
              	fail: err => {
                  this.copy(str)
                },
              	complete: () => {
                  this.clickShareLinking = false
                }
              });
            } else {
              this.clickShareLinking = false
              this.copy(str)
            }
          },
          fail: () => {
            this.clickShareLinking = false
          	this.copy(str)
          }
        })
      },
      clickSharePoster() {
        this.hideSharePop()
        this.posterData = {
          show: true,
          code: 'platform',
          url: this.$constant.shareUrl + '?pid=' + this.userInfo.id
        }
      },
      copy(str) {
        uni.setClipboardData({
        	data: str,
          success: () => {
            uni.showModal({
              title: '复制成功',
              content: '链接已复制，请手动粘贴发送给好友',
              showCancel: false
            })
          },
          fail: () => {
            uni.showModal({
              title: '复制失败',
              content: str,
              showCancel: false
            })
          },
          complete: () => {
          	this.hideSharePop()
          }
        })
      },
      gotoIndex() {
        uni.switchTab({
        	url: '/pages/shop/index'
        })
      }
    }
	}
